import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    api: "http://localhost",
    port: 8080,
    batch_service:"api/batch/",
    logs_service:"api/logs/",

    regex_ip: "^((25[0-5]|(2[0-4]|1[0-9]|[1-9]|)[0-9])(\\.(?!$)|$)){4}$",
    regex_date: "(\\d{4}-\\d{2}-\\d{2} \\d{2}:\\d{2}:\\d{2}(?:.\\d{3}\\b)?)$"
  },
  getters:{
    getBatchService: state => {
      return `${state.api}:${state.port}/${state.batch_service}`
    },
    getLogService: state => {
      return `${state.api}:${state.port}/${state.logs_service}`
    },

    getRegexDate: state => {
      return new RegExp(state.regex_date);
    },
    getRegexIp: state => {
      return new RegExp(state.regex_ip);
    }
  },
  mutations: {
  },
  actions: {
  },
  modules: {
  }
})
