<template>
  <div class="formLog">
    <ul :class="{'disable':true,'error':checkErrorClass(),'ok': submit.status }">
      <li v-for="(err, index) in errors" :key="`error_id_${index}`">
        {{ err }}
      </li>
    </ul>

    <form id="logManualInsert"  v-on:submit.prevent="checkForm" method="get"> 
    <ul>
        <li class="half">Date</li>
        <li class="half">Ip</li>
        <li class="half"><input type="text" name="date" v-model="date" placeholder="yyyy-MM-dd HH:mm:ss.SSS"></li>
        <li class="half"><input type="text" name="ip" v-model="ip" placeholder="Ipv4"></li>

        <li class="half">Request</li>
        <li class="half">Status</li>
        <li class="half"><input type="text" name="request" v-model="request" placeholder="Http request type"></li>
        <li class="half"><input type="number" name="status" v-model="status" placeholder="Http Code"></li>

        <li>User Agent</li>
        <li><input type="text" name="user_agent" v-model="user_agent" placeholder="User Agent"></li>

        <li class="send"><input type="submit" value="Submit" class="submitLog"></li>

    </ul>
    </form>

  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'FormLog',
  props: {
    msg: String
  },
  data(){
    return{

        dateChecker: new RegExp("(\\d{4}-\\d{2}-\\d{2} \\d{2}:\\d{2}:\\d{2}(?:.\\d{3}\\b)?)$"),
        ipChecker: new RegExp("^((25[0-5]|(2[0-4]|1[0-9]|[1-9]|)[0-9])(\\.(?!$)|$)){4}$"),

        errors:[],
        submit:{
          status: false
        },

        date: null,
        ip: null,
        request: null,
        status: null,
        user_agent: null
    }
  },
  methods:{
    
    checkErrorClass: function(){
        return this.errors.length > 0;
    },

    checkForm: function (e) {
      this.errors = [];
      this.submit.status = false;

      if(!this.dateChecker.test(this.date)){
        this.errors.push("The Date field must follows yyyy-MM-dd HH:mm:ss.SSS format")
        this.date = null;
      }

      if(!this.ipChecker.test(this.ip)){
        this.errors.push("The IP field must follows IPv4 format")
        this.ip = null;
      }

      if(this.user_agent != null){
        if(this.user_agent.length >= 255){
          this.errors.push("The user_agent must smaller than 255 characters")
          this.user_agent = null;
        }
      }

      if(this.errors.length == 0){
        this.submitToApi();
      }else{
        this.displayerrors = true;
      }

      e.preventDefault();
    },

    getLogModel: function(){
      return {
          "date": this.date,
          "ip": this.ip,
          "request": this.request,
          "status": this.status,
          "user_agent": this.user_agent
      }
    },


    submitToApi: function(){
      this.errors = [];
      this.submit.status = false;

      let headers = new Headers({
          "Content-Type":	"application/json"
      });

      fetch(this.getLogService, {
                    method: 'POST',
                    headers: headers,
                    body: JSON.stringify(this.getLogModel())
                }).then((response) => {
                    console.log(response);
                    if(response.status == 201){
                      this.submit.status = true;
                      this.errors.push("Log has been saved");
                    }else{
                        this.submit.status = false;
                    }

                    return response.json();
                }).then((data) => {

                    if('error_list' in data){
                      this.errors.push(data.error_list[0].message)
                    }
                    console.log(data);
                })
                .catch((error) => {
                    this.errors.push(error);
                    console.error(error);
                });
    }
  },
  computed: {

    ...mapGetters([
      'getLogService',

    ])
  }
}
</script>

